import simplebar from "simplebar-vue";
// import ChartData from "./data-saas";

import { DashboardService } from '../../services'
const dashboardService = new DashboardService();

export default {
  components: {
    simplebar
  },
  data() {
    return {
      // ChartData,
      title: "Meu Controlle",
      
      produtos_mais_vendidos:[],
      spinner_produtos_mais_vendidos: true,

      vendas_periodos:[],
      spinner_vendas_periodos: true,

      produtos_estoque_baixo: [],
      spinner_produtos_estoque_baixo: true,

      evolucao_12_meses: {
        series: [],
        chartoptions: {
          chart: {
            height: 350,
            type: "area",
            toolbar: {
              show: false,
            },
          },
          colors: ["#556ee6", "#f1b44c",'#cacaca'],
          dataLabels: {
            enabled: false,
          },
          stroke: {
            curve: "smooth",
            width: 2,
          },
          fill: {
            type: "gradient",
            gradient: {
              shadeIntensity: 1,
              inverseColors: false,
              opacityFrom: 0.45,
              opacityTo: 0.05,
              stops: [20, 100, 100, 100],
            },
          },
          xaxis: {
            categories: [
              "-",
            ],
          },
          markers: {
            size: 3,
            strokeWidth: 3,

