            position: "top",
            horizontalAlign: "right",
          },
        },
      },
      spinner_evolucao_12_meses: true,

      contas:{
        pagar: [],
        pagar_atrasadas: [],
        receber: [],
        receber_atrasadas: []
      }

    };
