  methods: {

     buscarProdutosEstoqueBaixo: function(){
      dashboardService.buscarProdutosEstoqueBaixo({}).then(response => {
        this.produtos_estoque_baixo = response.data;
        this.spinner_produtos_estoque_baixo = false;
        this.$forceUpdate();
      }).finally(() => setTimeout(() => {
        this.buscarProdutosEstoqueBaixo();
      }, 600000))
    },
    
    buscarProdutosMaisVendidos: function(){
      dashboardService.buscarProdutosMaisVendidos({}).then(response => {
        this.produtos_mais_vendidos = response.data;
        this.spinner_produtos_mais_vendidos = false;
        this.$forceUpdate();
      }).finally(() => setTimeout(() => {
        this.buscarProdutosMaisVendidos();
      }, 600000))
    },

    evolucao12meses: function(){
      dashboardService.evolucao12meses({}).then(response => {
        this.evolucao_12_meses.series = [
          {name: 'Total em Vendas', data: response.data.notas.map(x => Number(x.valor_total))},
          {name: 'Total em Serviços', data: response.data.servicos.map(x => Number(x.valor_total))},
          {name: 'Total Geral', data: response.data.faturamentos.map(x => Number(x.valor_total))},
        ] ;
        
        // this.evolucao_12_meses.chartoptions.xaxis.categories =  response.data.servicos.map(x => x.mesExtenso + '/'+x.ano);
        this.evolucao_12_meses.chartoptions.xaxis.categories =  response.data.labels;
          


        this.spinner_evolucao_12_meses = false;
        this.$forceUpdate();
      }).finally(() => setTimeout(() => {
        this.evolucao12meses();
      }, 600000))
    },

    buscarVendasPeriodos: function(){
      dashboardService.buscarVendasPeriodos({}).then(response => {
        this.vendas_periodos = response.data;

        this.vendas_periodos.hoje_diferenca = 0;
        this.vendas_periodos.semana_diferenca = 0;
        this.vendas_periodos.mes_diferenca = 0;

        if(this.vendas_periodos.ontem > 0){
          this.vendas_periodos.hoje_diferenca = ((this.vendas_periodos.hoje / this.vendas_periodos.ontem) - 1) * 100;
        }

        if(this.vendas_periodos.semana_anterior > 0){
          this.vendas_periodos.semana_diferenca = ((this.vendas_periodos.semana_atual / this.vendas_periodos.semana_anterior) - 1);
        }

        if(this.vendas_periodos.mes_anterior > 0){
          this.vendas_periodos.mes_diferenca = ((this.vendas_periodos.mes_atual / this.vendas_periodos.mes_anterior) - 1) * 100;
        }

        this.spinner_vendas_periodos = false;
        this.$forceUpdate();
      }).finally(() => setTimeout(() => {
        this.buscarVendasPeriodos();
      }, 300000))
    },

    buscarContas: function(){
      dashboardService.buscarContas({}).then(response => {
        console.log(response.data)
        this.contas = {
          pagar: response.data.pagar.map(p => { p.saldo = p.valor - p.valor_pago; return p}),
          pagar_atrasadas: response.data.pagar_atrasadas.map(p => { p.saldo = p.valor - p.valor_pago; return p}),
          receber: response.data.receber.map(p => { p.saldo = p.valor - p.valor_pago; return p}),
          receber_atrasadas: response.data.receber_atrasadas.map(p => { p.saldo = p.valor - p.valor_pago; return p}),
        }      
       
      }).finally(() => setTimeout(() => {
        this.buscarContas();
      }, 600000))
    }
  },
  
  mounted() {
       var container = document.querySelector(
